import pluralize from '~/utils/pluralize'

export default {
	props: {
		home: {
			type: Object,
			required: true
		}
	},
	mounted() {
		if (this.$route.query.result == 'success') {
			alert('Success!')
		}
	},
	methods: {
		pluralize,
		checkout() {
			const start = this.range.start.getTime() / 1000
			const end = this.range.end.getTime() / 1000
			this.$stripe.checkout(this.home.objectID, start, end)
		}
	},
	data() {
		return {
			range: {
				start: null,
				end: null
			}
		}
	}
}
